import textNew from "@/template/textNew.vue"
import newList from "@/template/newsList.vue"
import bigCover from "@/template/bigCover.vue"
import threeNew from "@/template/three.vue"
import hasConNew from "@/template/hasConNew.vue"
import activityList from "@/template/activityList.vue"
export default {
	components: {
		activityList,
		textNew,
		newList,
		bigCover,
		threeNew,
		hasConNew,
	},
	data() {
		return {
			newsList: [],
			cacheTab: [],
			tabIndex: 0,
			screenHeight: 0, //屏幕高度
			isLoading: false, //防止频繁触发
			bottomDistinct: 0, //距离底部多少像素时触发
			pageObj: {
				page: 0,
				pagecount: 0
			},
			noMore: false,
			isScroll: false,
			id: '',
			tabBars: [ {
				name: '资讯',
				id: 'zixun',
			},{
				name: '活动',
				id: 'huodong',
			}],
			allData: [
				[],
				[]
			],
			scrollInto: "",
		}
	},
	onLoad() {
		new Promise((resolve, reject) => {
			this.tabBars.forEach((tabBar) => {
				this.newsList.push({
					data: [],
					pageObj: {
						page: 1,
						pagecount: 1
					},
					isLoading: false,
					noMore: true
				});
			});
			resolve()
		}).then(() => {
			this.getAll();
		})
	},
	onPageScroll(e) {
		if (e.scrollTop > 100) {
			this.isScroll = true;
		} else {
			this.isScroll = false;
		}
	},
	methods: {
		getAll() {
			var that = this;
			this.$apiJs.myCollection({
				page: this.pageObj.page + 1
			}).then(res => {
				var r_data = res.backlist;
				r_data.forEach((item) => {
					// 1：资讯  2：活动 
					item.id=item.to_id;
					if (item.type == 1) {
						if (item.img.length && item.img) {
							item.cover = item.img[0].url
						} else {
							item.img = 'no'
							item.cover = 'no'
						}
						item.time = item.create_time;
						this.allData[0].push(item)
					} else {
						item.name =item.activity_name;
						this.allData[1].push(item)
					}
				})
			})
		},
		ontabtap(e) {
			let index = e.target.dataset.current || e.currentTarget.dataset.current;
			this.switchTab(index);
		},
		ontabchange(e) {
			let index = e.target.current || e.detail.current;
			this.switchTab(index);
		},
		switchTab(index) {
			var that = this;
			this.tabIndex = index;
			this.scrollInto = this.tabBars[index].id;
		}
	}
}
